.pricing_top{
    text-align: center;
}
.pricind_top h2{
    font-size: 2.5rem;
    margin-bottom: 20px;
}
.pricind_top p{
    font-size: 1.1rem;
    color: var(--heading-color);
    line-height: 28px;
    margin-bottom: 50px;
}
.pricing_wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 2rem;
}
.pricing_item{
    box-shadow:rgba(0,0,0,0.15)  0px 5px 15px 0px ;
    border-radius: 5px;
    width: 33%;
    background: #fff;
}
.pricing_item:hover{
    transform: translateY(-10px);
    box-shadow: 0px 0px 20px rgba(0,0,0,0.15);
}

.pricing_card_top {
    padding: 30px;
    border-bottom: 1px solid #d3cef2;
    text-align: center;
 }
 .pricing_card_top h2 {
    font-size: 1.5rem;
    margin-bottom: 0.9rem;
}
.pricing_card_top .pricing {
    font-size: 1.4rem;
}
.pricing_card_top .pricing span {
    font-size: 1rem;
    color: #666;
}
   
.pricing_item .services {
    padding: 30px;
}
.pricing_item .services ul li {
    display: flex;
    align-items: center;
    column-gap: 1.5rem;
    columns: #666;
    line-height: 28px;
}
.pricing_item .services ul li span i {
    font-size: 0.7rem;
}
.services {
    text-align: center;
}
.services button {
    margin-top: 50px;
}
    