:root{
--primary-color:#6f55f2;
--heading-color:#132742;
}
body{
  font-family: 'Times New Roman', Times, serif;
}
section{
  padding: 50px 0px;
}
a{
  text-decoration:none ;
  color: unset;
}
a:hover{
  color: unset;
}
link{
  text-decoration:none ;
  color: unset;
}
link:hover{
  color: unset;
}
ul{
  list-style: none;
}
.container{
  width:1140px;
  padding: 0px 15px;
  margin: auto;
}
.section_title{
  color: vae(--heading-color);
  font-size: 700;
}
.register-btn{
 background-color: var(--primary-color);
 border: none;
 border-radius: 5px;
 color: #fff;
 cursor: pointer;
 font-family: var(--font-name); 
 font-size: 1rem;
 font-weight: 700;
 outline: none;
 padding: 7px 15px;
}
.back{
  font-size: 20px;
  padding: 30px;
  cursor: pointer;

}
.back:hover{
  color: #6f55f2;
}