.footer{
    padding: 50px 0px;
    background-color: #eeeaff;
    width: 100%;
    margin-top: auto;
}
.footer_wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    column-gap: 2rem;
    background-color:#eeeaff ;
}
.footer_box{
    width: 30%;
}
.footer_box p{
    line-height: 1.2rem;
    margin-top: 20px;
}

.logo img{
    width: 5rem;
}
.logo h2{
    cursor: pointer;
}
.logo h2:hover{
    color: var(--primary-color);
}
.footer_title{
    font-size: 1.2rem;
    margin-bottom: 20px;
    margin-left: 35px;
}
.footer_links li{
    line-height: 28px;
    color: var(--heading-color);
}
.footer_links li a{
    cursor: pointer;
}
.footer_links li a:hover{
    color: var(--primary-color);
}